import { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import scss from 'styles/index.module.scss';
import SvgSprite from 'images/sprite.svg';
import { useAuth } from 'hooks';
import { indigo, pink, lightGreen, grey } from '@mui/material/colors';
import { Radio, InputAdornment } from '@mui/material';
import dayjs from 'dayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { useParams } from 'react-router-dom';

export const AddCard = ({
  modalTitle,
  idColumn,
  id,
  cardTitle,
  description,
  priority,
  deadline,
  modalBtnTitle,
  onClose,
  operation,
}) => {
  const { user } = useAuth();
  const dispatch = useDispatch();
  const { boardName } = useParams();

  const [titleValue, setTitleValue] = useState(cardTitle); // –¥–ª—è —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è
  const [descriptionValue, setDescriptionValue] = useState(description); // –¥–ª—è —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è
  const [selectedPriority, setSelectedPriority] = useState(
    priority ? priority : 'without'
  );
  const [inputWidth, setInputWidth] = useState('auto');
  const [isCalendarOpen, setIsCalendarOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState(
    deadline ? dayjs(deadline, 'DD/MM/YYYY') : dayjs()
  ); // dayjs() - currentDate

  //selectedDate = M¬†{$L: 'en', $u: undefined, $d: Tue Aug 29 2023 08:05:53 GMT+0300 (–∑–∞ —Å—Ö—ñ–¥–Ω–æ—î–≤—Ä–æ–ø–µ–π—Å—å–∫–∏–º –ª—ñ—Ç–Ω—ñ–º —á–∞—Å–æ–º), $x: {‚Ä¶}, $y: 2023,¬†‚Ä¶}
  // deadline ? dayjs(deadline, 'DD/MM/YYYY').format("dddd, MMMM D") : dayjs()

  // console.log(deadline) //01/09/2023
  // console.log(dayjs(deadline)) // M¬†{... Mon Jan 09 2023 00:00:00 ...} - –º—ñ–Ω—è—î –¥–µ–Ω—å —ñ –º—ñ—Å—è—Ü—å –º—ñ—Å—Ü—è–º–∏
  // console.log(dayjs(deadline, 'DD/MM/YYYY')); // M¬†{... Fri Sep 01 2023 00:00:00 ...} - —Ç–µ–ø–µ—Ä –≤—Å–µ –û–∫
  // –¢–µ–ø–µ—Ä –±—ñ–±–ª—ñ–æ—Ç–µ–∫–∞ Material UA –∑–∞—Å—Ç–æ—Å—É—î —Ñ–æ—Ä–º–∞—Ç "dddd, MMMM D" —ñ –æ—Ç—Ä–∏–º–∞—î–º–æ —Ç–µ, —â–æ —Ç—Ä–µ–±–∞:
  // console.log(dayjs(deadline, 'DD/MM/YYYY').format("dddd, MMMM D")); // Thursday, August 31

  // "Today, September 01" –∞–±–æ "Saturday, September 02"
  const dateFormat =
    dayjs(selectedDate).format('dddd, MMMM D') ===
    dayjs().format('dddd, MMMM D')
      ? '[Today,] MMMM D'
      : 'dddd, MMMM D';

  //------------------------------------- Submit -------------------------------------------

  const handleFormSubmit = event => {
    // –≤—ñ–¥–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–∏—Ö
    event.preventDefault();

    const inputTitle = event.target.elements.title.value.trim();
    const inputDescription = event.target.elements.description.value.trim();
    const inputPriority = event.target.elements.priority.value;
    const inputDeadline = dayjs(selectedDate).format('DD/MM/YYYY'); //   29/11/2023

    const cardData = {

      title: inputTitle,
      description: inputDescription,
      priority: inputPriority,
      deadLine: inputDeadline,
    };
    // {title: 'The Watch Spot Design', description: "Create a visually stunning and eye-catching watch dial design that embodies our brand's", priority: 'without', deadline: '11/10/2023', taskOwner: '64f548eb24765a1e4837366e'}
    // –≤—ñ–¥–ø—Ä–∞–≤–∫–∞ –Ω–∞ –±–µ–∫–µ–Ω–¥, –∞ –ø–æ—Ç—ñ–º –≤ —Å—Ç–æ—Ä —Ä–µ–¥–∞–∫—Å–∞
    if (id) {
      dispatch(
        operation({
          ...cardData,
          taskOwner: idColumn,
          idTask: id,
          boardId: boardName,
        })
      );
    } else {
      dispatch(
        operation({ ...cardData, taskOwner: idColumn, boardId: boardName })
      );
    }

    event.target.reset();

    if (event.currentTarget === event.target) {
      onClose();
      document.body.style.overflow = 'visible'; //body –ø–æ—á–Ω–µ —Å–∫—Ä–æ–ª–∏—Ç–∏—Å—è –ø—ñ—Å–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª–∫–∏
    }
  };

  // --------------- –ø—Ä—ñ–æ—Ä–∏—Ç–µ—Ç–Ω—ñ—Å—Ç—å ----------------------

  const controlProps = item => ({
    // low, medium, high, without
    value: item,
    onChange: e => setSelectedPriority(e.target.value),
    checked: selectedPriority === item,
    name: 'priority',
    inputProps: { 'aria-label': item },
  });

  const priorityStyles = {
    low: indigo[200],
    medium: pink[200],
    high: lightGreen[200],
    without: grey[400],
  };

  //------------------ –®–∏—Ä–∏–Ω–∞ —ñ–Ω–ø—É—Ç–∞ DatePicker---------------------
  useEffect(() => {
    const contentWidth = selectedDate.format(dateFormat).length * 9;
    setInputWidth(`${contentWidth}px`);
  }, [selectedDate, dateFormat]);

  return (
    <div className={scss.OBAddContainer} data-theme={user.theme}>
      <h4 className={scss.OBAddTitle}>{modalTitle}</h4>
      <form onSubmit={handleFormSubmit} autoComplete="off">
        <label>
          <input
            type="text"
            name="title"
            placeholder="Title"
            autoFocus
            required
            className={scss.OBAddInput}
            defaultValue={titleValue} // –¥–ª—è —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è
            onChange={e => setTitleValue(e.target.value)} // –¥–ª—è —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è
          />
        </label>
        <label>
          <textarea
            type="text"
            name="description"
            placeholder="Description"
            className={`${scss.OBAddInput} ${scss.OBAddDescription}`}
            defaultValue={descriptionValue} // –¥–ª—è —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è
            onChange={e => setDescriptionValue(e.target.value)} // –¥–ª—è —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è
          />
        </label>

        <div className={scss.OBAddlabel}>
          Label color
          <div className={scss.OBAddRadioGroup}>
            {Object.keys(priorityStyles).map(
              (
                priority //['low', 'medium', 'high', 'without']
              ) => (
                <Radio
                  className={scss.OBAddradioBtn}
                  key={priority}
                  {...controlProps(priority)}
                  sx={{
                    color: priorityStyles[priority],
                    '&.Mui-checked': {
                      color: priorityStyles[priority],
                    },
                    '&.Mui-checked .MuiSvgIcon-root': {
                      fontSize: '17px',
                    },
                    '&:not(.Mui-checked) .MuiSvgIcon-root': {
                      fontSize: '14px',
                      backgroundColor: priorityStyles[priority],
                      borderRadius: '50%',
                    },
                  }}
                />
              )
            )}
          </div>
        </div>

        <div>
          <p className={scss.OBAddlabel}>Deadline</p>
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            {/* dateAdapter={AdapterDayjs} - –≤–∫–∞–∑—É—î–º–æ, —â–æ –ø—ñ–¥ –∫–∞–ø–æ—Ç–æ–º –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–æ –±—ñ–±–ª—ñ–æ—Ç–µ–∫—É Day.js (—ó—ó –º–µ—Ç–æ–¥–∏) */}
            <DatePicker
              open={isCalendarOpen}
              onClose={() => setIsCalendarOpen(false)} // –∑–∞–∫—Ä–∏—Ç—Ç—è –∫–∞–ª–µ–Ω–¥–∞—Ä—è
              onChange={date => setSelectedDate(date)} //Material UA –≤ date –∑–∞–ø–∏—Å—É—î –æ–±—Ä–∞–Ω—É –∑ –∫–∞–ª–µ–Ω–¥–∞—Ä—è –¥–∞—Ç—É
              // M¬†{$L: 'en', $u: undefined, $d: Fri Sep 01 2023 09:13:29 GMT+0300 (–∑–∞ —Å—Ö—ñ–¥–Ω–æ—î–≤—Ä–æ–ø–µ–π—Å—å–∫–∏–º –ª—ñ—Ç–Ω—ñ–º —á–∞—Å–æ–º), $x: {‚Ä¶}, $y: 2023,¬†‚Ä¶}
              value={selectedDate}
              format={dateFormat} // –ø—Ä–∏–π–º–∞—î —Ä—è–¥–æ–∫
              disablePast={true} // –º–∏–Ω—É–ª—ñ –¥–∞—Ç–∏ –Ω–µ –æ–±–∏—Ä—É—Ç—å—Å—è
              outsideCurrentMonth={true} // –ø–æ—á–∞—Ç–æ–∫ –Ω–∞—Å—Ç—É–ø–Ω–æ–≥–æ –º—ñ—Å—è—Ü—è –Ω–µ–≤–∏–¥–∏–º–∏–π
              dayOfWeekFormatter={day => day.slice(0, 2).toUpperCase()} // –≤–∏–¥–∏–º—ñ –ø–µ—Ä—à—ñ 2 –ª—ñ—Ç–µ—Ä–∏ –Ω–∞–∑–≤–∏ –¥–Ω—è —Ç–∏–∂–Ω—è
              slots={{
                openPickerButton: () => null, // –ø—Ä–∏—Ö–æ–≤—É—î–º–æ –¥–µ—Ñ–æ–ª—Ç–Ω—É –∫–Ω–æ–ø–∫—É-—ñ–∫–æ–Ω–∫—É –∫–∞–ª–µ–Ω–¥–∞—Ä—è
              }}
              slotProps={{
                popper: {
                  placement: 'bottom-start',
                  sx: {
                    '.css-71vzt-MuiPaper-root-MuiPickersPopper-paper': { // –ø–æ–ª–æ—Ç–Ω–æ –∫–∞–ª–µ–Ω–¥–∞—Ä—è
                      // backgroundColor: 'red', // –ø—Ä–∞—Ü—é—î
                      border: '1px solid',
                      borderColor: user.theme === 'violet' ? '#585bbe' : '#bedfad',
                      borderRadius: '8px',
                      padding: '18px',
                      width: '233px',
                      height: '254px',
                    },
                    '.MuiPickersLayout-root.': {
                      width: '197px',
                      // hight: '110px',
                    },
                    '.MuiDateCalendar-root': {
                      // –ø–æ–ª–æ—Ç–Ω–æ –∫–∞–ª–µ–Ω–¥–∞—Ä—è –±–µ–∑ –∑–æ–≤–Ω—ñ—à–Ω—ñ—Ö –ø–∞–¥—ñ–Ω–≥—ñ–≤
                      // backgroundColor: 'orange',
                      width: '197px',
                      hight: '166px',
                    },

                    '.css-cwhad8-MuiDateCalendar-root': {  // –ø–æ–ª–æ—Ç–Ω–æ –∫–∞–ª–µ–Ω–¥–∞—Ä—è –±–µ–∑ –∑–æ–≤–Ω—ñ—à–Ω—ñ—Ö –ø–∞–¥—ñ–Ω–≥—ñ–≤

                      height: '215px',
                    },
                    '.MuiPickersLayout-contentWrapper': {
                      width: '197px',
                      // hight: '166px',
                    },
                    '.MuiPickersCalendarHeader-root': {
                      // –º—ñ—Å—è—Ü—å —ñ –¥–∞—Ç–∞
                      // backgroundColor: 'yellowgreen',
                      textAlign: 'center',
                      borderBottom: '1px solid rgba(22, 22, 22, 0.20)',
                    },
                    '.MuiPickersCalendarHeader-label': {
                      margin: 0,
                    },
                    '.MuiTypography-root': {
                      // –¥–Ω—ñ —Ç–∏–∂–Ω—è
                      // backgroundColor: 'violet',
                      marginTop: '14px',
                      width: '23px',
                      height: '23px',
                    },
                    '.MuiDayCalendar-monthContainer': {
                      // –ø–æ–ª–æ—Ç–Ω–æ –¥–∞—Ç
                      // backgroundColor: 'red',
                      width: '197px',
                      hight: '166px',
                    },
                    '.MuiDayCalendar-weekContainer': {
                      // backgroundColor: 'blue',
                      marginBottom: '3px',
                    },
                    '.MuiPickersDay-dayWithMargin': {
                      // –¥–Ω—ñ —ñ–Ω—à–æ–≥–æ –º—ñ—Å—è—Ü—è
                      width: '23px',
                      height: '23px',
                    },
                    '.MuiButtonBase-root': {
                      // –æ–¥–∏–Ω –¥–µ–Ω—å
                      width: '23px',
                      height: '23px',
                    },
                    '.MuiPickersDay-root': {
                      // —É—Å—ñ –¥–Ω—ñ
                      // backgroundColor: 'green',
                      fontFamily: 'Poppins',
                      fontSize: '14px',
                      fontweight: '400',
                      lineHeight: '18px',
                    },
                    '.MuiPickersDay-root.Mui-selected': {
                      // –≤–∏–±—Ä–∞–Ω–∏–π –¥–µ–Ω—å
                      backgroundColor:
                        user.theme === 'violet' ? '#585bbe' : '#bedfad',
                      '&:hover': {
                        backgroundColor:
                          user.theme === 'violet' ? '#8d8fc9' : '#9fd186',
                      },
                    },
                  },
                },
                calendarHeader: {
                  sx: {
                    '.MuiPickersCalendarHeader-root': {
                      position: 'relative',
                    },
                    '.MuiPickersCalendarHeader-labelContainer': {
                      // —Ü–µ —Ç—ñ–ª—å–∫–∏ –º—ñ—Å—è—Ü—å —ñ —Ä—ñ–∫
                      // backgroundColor: 'green',
                      fontFamily: 'Poppins, sans-serif',
                      fontSize: '16px',
                      fontWeight: 500,
                      letterSpacing: '-0.32px',
                      display: 'grid', // —Ç–µ–ø–µ—Ä –º—ñ—Å—è—Ü—å —ñ —Ä—ñ–∫ —Ü–µ–Ω—Ç—Ä–æ–≤–∞–Ω—ñ
                    },
                    '.MuiPickersCalendarHeader-label': {
                      display: 'inline-block',
                    },
                    '.MuiPickersCalendarHeader-switchViewButton': {
                      display: 'none', // –ø—Ä–∏–±–∏—Ä–∞—î–º–æ —Å—Ç—Ä—ñ–ª–∫—É –≤–Ω–∏–∑ –ø–æ—Ä—è–¥ –∑ –º—ñ—Å—è—Ü–µ–º —ñ —Ä–æ–∫–æ–º
                    },
                    '.MuiIconButton-edgeEnd': {
                      // –∫–Ω–æ–ø–∫–∞ <
                      position: 'absolute',
                      top: '17px',
                      left: '14px',
                    },
                    '.MuiIconButton-edgeStart': {
                      // –∫–Ω–æ–ø–∫–∞ >
                      position: 'absolute',
                      top: '17px',
                      right: '12px',
                    },
                  },
                  style: {
                    display: 'inline',
                    margin: 0,
                    padding: 0,
                  },
                },

                previousIconButton: {
                  sx: {
                    stroke: user.theme === 'violet' ? '#5255BC' : '#BEDBB0',
                  },
                },
                nextIconButton: {
                  sx: {
                    stroke: user.theme === 'violet' ? '#5255BC' : '#BEDBB0',
                  },
                },

                textField: {
                  fullWidth: false,
                  onClick: () => setIsCalendarOpen(true), // —ñ–Ω–ø—É—Ç —Å—Ç–∞—î –∫–ª—ñ–∫–∞–±–µ–ª—å–Ω–∏–º; –ø–æ –∫–ª—ñ–∫—É –≤—ñ–¥–∫—Ä–∏–≤–∞—î—Ç—å—Å—è –∫–∞–ª–µ–Ω–¥–∞—Ä (picker)
                  variant: 'standard',
                  size: 'small',
                  sx: {
                    width: inputWidth,
                    minWidth: '130px',
                    '.css-nz481w-MuiInputBase-input-MuiInput-input': {
                      // —ñ–Ω–ø—É—Ç –±–µ–∑ –∫–Ω–æ–ø–∫–∏
                      padding: 0,
                    },
                  },
                  InputProps: {
                    disableUnderline: true, // –ø—Ä–∏–±–∏—Ä–∞—î –¥–µ—Ñ–æ–ª—Ç–Ω–∏–π –Ω–∏–∂–Ω—ñ–π –±–æ—Ä–¥–µ—Ä, –≤—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–π variant: 'standard'
                    'aria-label': 'deadline',
                    style: {
                      fontSize: '14px',
                      fontFamily: 'Poppins, sans-serif',
                      color: user.theme === 'violet' ? '#5255BC' : '#BEDBB0', // –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É
                      fontWeight: 500,
                    },
                    // —Ä–æ–∑—Ç–∞—à—É–≤–∞–Ω–Ω—è —ñ–∫–æ–Ω–∫–∏ - –≤ –∫—ñ–Ω—Ü—ñ —ñ–Ω–ø—É—Ç—É (—î —ñ startAdornment)
                    endAdornment: (
                      <InputAdornment
                        position="start"
                        sx={{ cursor: 'pointer' }}
                      >
                        <svg className={scss.OBAddDateIcon} width="18px">
                          <use
                            href={SvgSprite + '#icon-chevron-down'} // —Å—Ç—Ä—ñ–ª–∫–∞ –≤–Ω–∏–∑ –≤ –∫–∞–ª–µ–Ω–¥–∞—Ä—ñ
                            aria-label="open calendar"
                            edge="start"
                          />
                        </svg>
                      </InputAdornment>
                    ),
                  },
                },
              }}
            ></DatePicker>
          </LocalizationProvider>
        </div>

        <button type="submit" className={scss.OBAddSubmitBtn}>
          <div className={scss.OBAddIconWrapper}>
            <svg className={scss.OBAddSubmitIcon}>
              <use href={SvgSprite + '#icon-plus'} />
            </svg>
          </div>
          {modalBtnTitle}
        </button>
      </form>
    </div>
  );
};